import {
  SET_LOADING,
  GET_TECHS,
  TECHS_ERROR,
  ADD_TECH,
  DELETE_TECH,
} from "./types";

// GET LOGS
export const getTechs = () => async (dispatch) => {
  try {
    setLoading();

    const res = await fetch("/techs");
    const data = await res.json();
    dispatch({
      type: GET_TECHS,
      payload: data,
    });
  } catch (err) {
    dispatch({
      type: TECHS_ERROR,
      payload: err.response.statusText,
    });
  }
};

//SET LOADING
export const setLoading = () => {
  return {
    type: SET_LOADING,
  };
};

//ADD TECH
export const addTech = (tech) => async (dispatch) => {
  try {
    setLoading();

    const res = await fetch("/techs", {
      method: "POST",
      body: JSON.stringify(tech),
      headers: { "Content-Type": "application/json" },
    });
    const data = await res.json();
    dispatch({
      type: ADD_TECH,
      payload: data,
    });
  } catch (err) {
    dispatch({
      type: TECHS_ERROR,
      payload: err.response.statusText,
    });
  }
};

//DELETE TECHS
export const deleteTech = (id) => async (dispatch) => {
  try {
    setLoading();

    const res = await fetch(`/techs/${id}`, {
      method: "DELETE",
    });
    const data = await res.json();
    dispatch({
      type: DELETE_TECH,
      payload: id,
    });
  } catch (err) {
    dispatch({
      type: TECHS_ERROR,
      payload: err.response.statusText,
    });
  }
};
